import React, { Component } from 'react';
import Request from "superagent";
import comments from './comments.css';
import  CsrfToken from '../csrfToken';

class Comments extends Component {
    constructor(props){
        super(props);
        this.state = {
            comments:[],
            newComment:"",
            csrfMiddleWareToken:'',
        }
        
    }
    fetchComments=()=>{
        const url = `api/v1/tweets/comment/${this.props.match.params.id}`;
        fetch(url)
        .then(response=>response.json())
        .then(data=>this.setState({
            comments: data
        }))
      }
    componentWillMount=()=>{
        this.fetchComments(this.props.match.params);
        console.log()
    }
    getToken=(token)=>{
        this.setState({csrfMiddleWareToken:token});
    }
    followUser=(e)=>{
        const url = `api/v1/accounts/follow/`;
        Request.post(url)
        .set("X-CSRFToken", this.state.csrfMiddleWareToken)
        .send({
            // 'csrfmiddlewaretoken': this.state.csrfMiddleWareToken,
            'to_follow':parseInt(e.target.id),
        })
        .then(response=>console.log("success"))
        .catch(console.error);
    }
    formSubmit=(e)=>{
        e.preventDefault();
        const url = 'api/v1/tweets/comment/add/';
        Request.post(url)
        .set("X-CSRFToken", this.state.csrfMiddleWareToken)
        .send({
            // 'csrfmiddlewaretoken': this.state.csrfMiddleWareToken,
            'tweet':parseInt(this.props.match.params.id),
            'comment':this.state.newComment
        })
        .then(response=>console.log("error"))
        .catch(console.error);


    }
    render() { 
        return ( 
            <div>
                <div className="row">
                    <div className="new-comment">
                        <form method="POST" onSubmit={this.formSubmit}>
                            <CsrfToken get_token={this.getToken}/>
                            <h4>Add a new comment</h4>
                            <textarea rows="5" cols="100" onChange={e=>this.setState({newComment:e.target.value})}>
                            
                            </textarea >
                            <input type="submit" className="btn" value="submit"/>
                        </form>
                        
                    </div>
                    <div className="comments">
                        <h3>Comments</h3>
                        {this.state.comments.length?
                            this.state.comments.map(comment=>{
                                return <div className="comment">
                                        <h4>User {comment.user}: {comment.comment}</h4>
                                        {parseInt(this.props.match.params.id)!==parseInt(comment.user)?
                                            <button className="btn" id={comment.user} onClick={this.followUser}>follow</button>
                                        :null}
                                    </div>
                            }):
                            <h4>There are no comments for this tweet!</h4>
                        }
                    
                    </div>
                </div>
            </div>
         );
    }
}



export default Comments;